.aPost {
  width: 500px;
  margin: 0 auto;
  background: yellow;
  margin-top: 30px;
  background: #fff;
  border: 1px #eaeaea solid;
  border-radius: 5px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  overflow: hidden; }
  .aPost:nth-of-type(1) {
    margin-top: 0; }
  .aPost .userName {
    font-size: 15px;
    font-weight: bold;
    padding: 10px 20px 1px; }
  .aPost .postDatetime {
    font-size: 11px;
    color: #aaa;
    padding: 1px 20px 10px; }
  .aPost .text {
    background: #fafbfc;
    padding: 10px 20px; }
